.container{
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  color: white;
}
.title{
  position: absolute;
  top: 15px;
  left: 22px;
  color: red;
  font-size: 33px;
  font-weight: 900;
  text-shadow: 2px 2px 6px #000;
  cursor: pointer;
}
.today{
  color: white;
  font-size: 21px;
  padding-top: 48px;
}
.movies{
  display: flex;
  margin-top: 15px;
  width: 1150px;
  overflow: scroll;
}
.movies::-webkit-scrollbar{
  display: none;
}

